/* ==========================================================================
   TYPE
   - Meat
   - Headings
   - Media Query adjustments
   ========================================================================== */

/* Meat
   ========================================================================== */

p {           
    font-size: $fsp;
    line-height: $lh; 
}

.intro {      
    font-size: $fs3; 
    line-height: 125%;  // not identical to our main line spacing which is 135%, but looks better this way
}

.post ol li {
    font-size: $fsp;
    line-height: $lh;
    margin-bottom: calc($lh / 3);
}

.post ul li {
    font-size: $fsp;
    line-height: $lh;
    margin-bottom: calc($lh / 3);
}



/* Headings
   ========================================================================== */

h1,.h1 {      font-size: $fs1; }
h2,.h2 {      font-size: $fs2; } 
h3,.h3 {      font-size: $fs3; } 
h4,.h4 {      font-size: $fs4; } 
h5,.h5 {      font-size: $fs5; } 
h6,.h6 {      font-size: $fs6; } 

.meta {       font-size: $fs6; }

.archive input { font-size: $fs2}



// Mobile

@media only screen and (max-width: $breakpoint-1-max-width) {

p {           
    font-size: $fs5;
    margin-bottom: $fs5;
    line-height: $lhsmall;
}

.intro {      
    font-size: $fs4; 
    margin-bottom: $fs4; 
    line-height: 125%;
}

.post ol li {
    font-size: $fs5;
    line-height: $lhsmall;
    margin-bottom: calc($lhsmall / 2);
}

.post ul li {
    font-size: $fs5;
    line-height: $lhsmall;
    margin-bottom: calc($lhsmall / 2);
}

h1,.h1 {      font-size: $fs2; }
h2,.h2 {      font-size: $fs3; } 
h3,.h3 {      font-size: $fs4; } 
h4,.h4 {      font-size: $fs5; } 
h5,.h5 {      font-size: $fs6; } 
h6,.h6 {      font-size: $fs6; } 

.meta {       font-size: $fs6; }

.archive input { font-size: $fs3}

}


